<template>
  <div>
    <el-row>
      <el-form ref="form" inline :model="form" label-width="100px">
        <el-form-item label="考公倍数">
          <el-select v-model="form.mul" placeholder="请选择倍数">
            <el-option label="无" :value="1"></el-option>
            <el-option v-for="(item, idx) in muls" :label="item.name" :value="item.value" :key="idx"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="对决功绩壹">
          <el-select v-model="form.level1" placeholder="请选择等级">
            <el-option label="无" :value="0"></el-option>
            <el-option v-for="(item, idx) in level1s" :label="item.name" :value="item.value" :key="idx"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="对决功绩贰">
          <el-select v-model="form.level2" placeholder="请选择等级">
            <el-option label="无" :value="0"></el-option>
            <el-option v-for="(item, idx) in level2s" :label="item.name" :value="item.value" :key="idx"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="对决功绩叁">
          <el-select v-model="form.level3" placeholder="请选择等级">
            <el-option label="无" :value="0"></el-option>
            <el-option v-for="(item, idx) in level3s" :label="item.name" :value="item.value" :key="idx"></el-option>
          </el-select>
        </el-form-item>
        <!-- <el-form-item label="其他功绩加成">
          <el-input v-model="form.other" placeholder="其他功绩加成，例如如果是双倍城皮填入: 2" type="number"></el-input>
        </el-form-item> -->
        <el-form-item label="我的氪金">
          <el-input v-model="form.money" placeholder="氪金直接记录已有功绩" type="number"></el-input>
        </el-form-item>
        <el-form-item label="已有功绩">
          <el-input v-model="form.old" :min="0" placeholder="当前功绩" type="number">
            <template slot="append">万</template>
          </el-input>
        </el-form-item>
        <el-form-item label="双倍城皮">
          <el-switch
            v-model="form.other"
            :active-value="2"
            :inactive-value="1"
            active-color="#13ce66"
            inactive-color="#ff4949">
          </el-switch>
        </el-form-item>
      </el-form>
    </el-row>
    <el-tabs v-model="activeName" @tab-click="handleClick">
      <el-tab-pane label="周一" name="1">
        <monday :form="form" :targets="targets" />
      </el-tab-pane>
      <el-tab-pane label="周二" name="2">
        <tuesday :form="form" :targets="targets" />
      </el-tab-pane>
      <el-tab-pane label="周三" name="3">
        <wednesday :form="form" :targets="targets" />
      </el-tab-pane>
      <el-tab-pane label="周四" name="4">
        <thursday :form="form" :targets="targets" />
      </el-tab-pane>
      <el-tab-pane label="周五" name="5">
        <friday :form="form" :targets="targets" />
      </el-tab-pane>
      <el-tab-pane label="周六" name="6">
        <saturday :form="form" :targets="targets" />
      </el-tab-pane>
    </el-tabs>
  </div>
</template>
<script>
import Friday from './Friday.vue';
import Monday from './Monday.vue';
import Saturday from './Saturday.vue';
import Thursday from './Thursday.vue';
import Tuesday from './Tuesday.vue';
import Wednesday from './Wednesday.vue';
  export default {
  components: { Monday, Tuesday, Wednesday, Thursday, Friday, Saturday },
    data() {
      return {
        activeName: '1',
        form: {
          level1: 0,
          level2: 0,
          level3: 0,
          mul: 1,
          target: 0,
          other: null,
          money: null,
          old: 0
        },
        level1s: Array.from({length: 5}, (item, index) => {return {name: `${index + 1}级`, value: (index + 1) * 0.1}}),
        level2s: Array.from({length: 10}, (item, index) => {return {name: `${index + 1}级`, value: (index + 1) * 0.1}}),
        level3s: Array.from({length: 10}, (item, index) => {return {name: `${index + 1}级`, value: (index + 1) * 0.15}}),
        muls: [
          {name: '1.5倍', value: 1.5},
          {name: '2倍', value: 2},
          {name: '3倍', value: 3},
        ],
        targets: [
          {name: '20万', value: 200000},
          {name: '80万', value: 800000},
          {name: '160万', value: 1600000},
          {name: '500万', value: 5000000},
          {name: '1000万', value: 10000000},
          {name: '2000万', value: 20000000},
          {name: '2400万', value: 24000000},
          {name: '3000万', value: 30000000},
          {name: '4000万', value: 40000000},
        ]
      };
    },
    methods: {
      handleClick(tab, event) {
      }
    }
  };
</script>
<style>
.text-danger {
  color: #F56C6C;
}
.text-success {
  color:#67C23A;
}
</style>


