<template>
    <div id="monday">
      <el-card class="box-card">
        <el-form>
            <el-form-item inline label="采集功绩对决">
              <el-select @change="calcRate" v-model="addtion" filterable placeholder="请选择等级">
                <el-option label="无" :value="0"></el-option>
                <el-option v-for="(item, idx) in addtions" :label="item.name" :value="item.value" :key="idx"></el-option>
              </el-select>
            </el-form-item>
          </el-form>
        <div slot="header" class="clearfix">
          <span>基础采集信息</span>
        </div>
        <el-table :data="tableData" style="width: 100%">
          <el-table-column prop="name" label="项目"></el-table-column>
          <el-table-column prop="score" label="基础分"></el-table-column>
          <el-table-column prop="totalScore" label="加倍分"></el-table-column>
        </el-table>
      </el-card>
      <el-card class="box-card">
        <!-- <div slot="header" class="clearfix">
          <span>需要资源</span>
          <el-form>
            <el-form-item inline label="金矿采集速度/每分钟">
              <el-input @input="calcTable" v-model="goldRate" :min="0" placeholder="每分钟采集数量" type="number"></el-input>
            </el-form-item>
            <el-form-item label="木材采集速度/每分钟">
              <el-input @input="calcTable" v-model="woodRate" :min="0" placeholder="每分钟采集数量" type="number"></el-input>
            </el-form-item>
            <el-form-item label="粮食采集速度/每分钟">
              <el-input @input="calcTable" v-model="foodRate" :min="0" placeholder="每分钟采集数量" type="number"></el-input>
            </el-form-item>
          </el-form>
        </div> -->
        <el-table stripe :data="newTableData" style="width: 100%">
          <el-table-column align="center" prop="name" width="70" label="档位"></el-table-column>
          <el-table-column align="center" label="需要资源">
            <template slot-scope="scope">
              <el-card class="box-card">
                <div class="text item">
                  {{'金矿: ' + scope.row.goldTotal }}万
                </div>
                <div class="text item">
                  {{'粮食: ' + scope.row.foodTotal }}万
                </div>
                <div class="text item">
                  {{'木材: ' + scope.row.woodTotal }}万
                </div>
              </el-card>
            </template>
          </el-table-column>
          <el-table-column align="center" width="60" label="达标">
            <template slot-scope="scope">
              <el-button v-if="scope.row.value == 0" type="success" size="small" icon="el-icon-check" circle></el-button>
              <el-button v-else size="small" icon="el-icon-close" circle></el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-card>
    </div>
</template>
<script>
  export default {
    props: {
      form: Object,
      targets: Array,
    },
    data() {
      return {
        activeName: '1',
        tableData: [
          {'name': '金矿', 'score': 800, 'totalScore': 800},
          {'name': '粮食', 'score': 600, 'totalScore': 600},
          {'name': '木材', 'score': 400, 'totalScore': 400},
        ],
        newOld: 0,
        newTableData: [],
        goldScore: 0,
        woodScore: 0,
        foodScore: 0,
        goldRate: 0,
        woodRate: 0,
        foodRate: 0,
        addtion: 0,
        addtions: Array.from({length: 10}, (item, index) => {return {name: `${index + 1}级`, value: (index + 1) * 0.15}})
      };
    },
    watch: {
      form: {
        deep: true,
        handler(newVal, oldVal) {
          this.calcRate();
        }
      }
    },
    methods: {
      calcRate() {
        const totalLevel = (this.form.level1 + this.addtion + this.form.level2 + this.form.level3 + this.form.mul) + Math.max(this.form.other - 1, 0);
        let tableData = this.tableData;
        for (let item of tableData) {
          item['totalScore'] = parseFloat(totalLevel * item['score']).toFixed(0);
          if (item.name == '金矿') {
            this.goldScore = item.totalScore;
          } else if (item.name == '粮食') {
            this.foodScore = item.totalScore;
          } else {
            this.woodScore = item.totalScore;
          }
        }
        let old = this.form.old * 10000;
        if (this.form.money > 0) {
          old += this.form.money * 3500;
        }
        this.newOld = old;
        this.calcTable();
      },
      // 计算缺少功绩
      calcTable() {
        const newTableData = [];
        for(const item of this.targets) {
          const value = Math.max(item.value - this.newOld, 0);
          const goldTotal = value / this.goldScore;
          const foodTotal = value / this.foodScore;
          const woodTotal = value / this.woodScore;
          newTableData.push({
            name: item.name,
            value: value,
            goldTotal: goldTotal.toFixed(1),
            woodTotal: woodTotal.toFixed(1),
            foodTotal: foodTotal.toFixed(1)
          });
        }
        this.newTableData = newTableData;
      }
    }
  };
</script>
<style>
  .text {
    font-size: 14px;
  }

  .item {
    padding: 18px 0;
  }
</style>


