<template>
    <div id="tuesday">
      <el-card class="box-card">
        <el-form>
          <el-form-item inline label="城建功绩对决">
            <el-select @change="calcRate" v-model="addtion" filterable placeholder="请选择等级">
              <el-option label="无" :value="0"></el-option>
              <el-option v-for="(item, idx) in addtions" :label="item.name" :value="item.value" :key="idx"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <span @click="drawer=true">我的加速: <span class="text-danger">{{ totalHour }}</span>时</span>
          </el-form-item>
        </el-form>
        <el-drawer
          title="我的加速"
          :visible.sync="drawer"
          size="50%"
          :with-header="false">
          <el-form>
            <el-form-item label="1分钟">
              <el-input type="number" @change="changeSpped" v-model="speed.m1">
                <template slot="append">个</template>
              </el-input>
            </el-form-item>
            <el-form-item inline label="5分钟">
              <el-input type="number" @change="changeSpped" v-model="speed.m5">
                <template slot="append">个</template>
              </el-input>
            </el-form-item>
            <el-form-item inline label="15分钟">
              <el-input type="number" @change="changeSpped" v-model="speed.m15">
                <template slot="append">个</template>
              </el-input>
            </el-form-item>
            <el-form-item inline label="1小时">
              <el-input type="number" @change="changeSpped" v-model="speed.m60">
                <template slot="append">个</template>
              </el-input>
            </el-form-item>
            <el-form-item inline label="8小时">
              <el-input type="number" @change="changeSpped" v-model="speed.m480">
                <template slot="append">个</template>
              </el-input>
            </el-form-item>
            <el-form-item inline label="24小时">
              <el-input type="number" @change="changeSpped" v-model="speed.m1440">
                <template slot="append">个</template>
              </el-input>
            </el-form-item>
          </el-form>
        </el-drawer>
        <div slot="header" class="clearfix">
          <span>城建基本信息</span>
        </div>
        <el-table :data="tableData" style="width: 100%">
          <el-table-column prop="name" label="项目"></el-table-column>
          <el-table-column prop="score" label="基础分"></el-table-column>
          <el-table-column prop="totalScore" label="加倍分"></el-table-column>
        </el-table>
      </el-card>
      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <span>需要加速(建筑分忽略)</span>
        </div>
        <el-table :data="newTableData" style="width: 100%">
          <el-table-column prop="name" label="档位"></el-table-column>
          <el-table-column prop="hours" label="总小时"></el-table-column>
          <el-table-column prop="finish" label="可达标">
            <template slot-scope="scope">
              <el-button v-if="scope.row.finish" type="success" size="small" icon="el-icon-check" circle></el-button>
              <span class="text-danger" v-else>-{{ scope.row.sub }}</span>
            </template>
          </el-table-column>
        </el-table>
      </el-card>
    </div>
</template>
<script>
  export default {
    props: {
      form: Object,
      targets: Array
    },
    data() {
      return {
        activeName: '1',
        tableData: [
          {'name': '建筑分', 'score': 1, 'totalScore': 1},
          {'name': '建筑加速', 'score': 100, 'totalScore': 100}
        ],
        newTableData: [],
        addtion: 0,
        addtions: Array.from({length: 10}, (item, index) => {return {name: `${index + 1}级`, value: (index + 1) * 0.15}}),
        drawer: false,
        old: 0,
        speed: {
          m1: null,
          m5: null,
          m15: null,
          m60: null,
          m480: null,
          m1440: null
        },
        totalHour: 0,
        speedScore: 0
      };
    },
    watch: {
      form: {
        deep: true,
        handler(newVal, oldVal) {
          this.calcRate();
        }
      }
    },
    methods: {
      changeSpped() {
        let hour = (parseInt(this.speed.m1 || 0) + parseInt(this.speed.m5 || 0) * 5 + parseInt(this.speed.m15 || 0) * 15 + parseInt(this.speed.m60 || 0) * 60 + parseInt(this.speed.m480 || 0) * 480 + parseInt(this.speed.m1440 || 0) * 1440) / 60;
        this.totalHour = hour.toFixed(2);
        this.calcRate();
      },
      calcRate() {
        const totalLevel = (this.form.level1 + this.addtion + this.form.level2 + this.form.level3 + this.form.mul) + Math.max(this.form.other - 1, 0);
        let tableData = this.tableData;
        for (let item of tableData) {
          item['totalScore'] = parseFloat(totalLevel * item['score']).toFixed(0);
          if (item.name == '建筑加速') {
            this.speedScore = item['totalScore'];
          }
        }
        let old = this.form.old * 10000;
        if (this.form.money > 0) {
          old += this.form.money * 3500;
        }
        this.old = old;
        this.calcTable();
      },
      // 计算缺少功绩
      calcTable() {
        const newTableData = [];
        for(const item of this.targets) {
          const minutes = Math.floor((item.value - this.old) / this.speedScore);
          const hours = Math.max((minutes / 60).toFixed(1), 0);
          const sub = Math.max(hours - this.totalHour, 0);
          newTableData.push({
            'name': item.name, 'hours': `${hours} 时`, 'minutes': minutes + ' 分',
            'finish': sub == 0, "sub": sub.toFixed(2)
          });
        }
        this.newTableData = newTableData;
      }
    }
  };
</script>


