<template>
    <div id="tuesday">
      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <span>招贤基本信息</span>
        </div>
        <el-table :data="tableData" style="width: 100%">
          <el-table-column prop="name" label="项目"></el-table-column>
          <el-table-column prop="score" label="基础分"></el-table-column>
          <el-table-column prop="totalScore" label="加倍分"></el-table-column>
        </el-table>
      </el-card>
      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <span>需要招贤次数</span>
        </div>
        <el-table stripe :data="newTableData" style="width: 100%">
          <el-table-column align="center" prop="name" width="70" label="档位"></el-table-column>
          <el-table-column align="center" label="需要次数">
            <template slot-scope="scope">
              <el-card class="box-card">
                <div class="text item">
                  {{'天下: ' + scope.row.count }}次
                </div>
                <div class="text item">
                  {{'势力/时运: ' + scope.row.otherCount }}次
                </div>
              </el-card>
            </template>
          </el-table-column>
          <el-table-column align="center" width="60" label="达标">
            <template slot-scope="scope">
              <el-button v-if="scope.row.count == 0" type="success" size="small" icon="el-icon-check" circle></el-button>
              <el-button v-else size="small" icon="el-icon-close" circle></el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-card>
    </div>
</template>
<script>
  export default {
    props: {
      form: Object,
      targets: Array,
    },
    data() {
      return {
        activeName: '1',
        tableData: [
          {'name': '天下', 'score': 40000, 'totalScore': 40000},
          {'name': '势力/时运', 'score': 50000, 'totalScore': 50000}
        ],
        airScore: 0,
        otherScore: 0,
        old: 0,
        newTableData: [],
        addtion: 0,
        addtions: Array.from({length: 10}, (item, index) => {return {name: `${index + 1}级`, value: (index + 1) * 0.15}})
      };
    },
    watch: {
      form: {
        deep: true,
        handler(newVal, oldVal) {
          this.calcRate();
        }
      }
    },
    methods: {
      calcRate() {
        const totalLevel = (this.form.level1 + this.addtion + this.form.level2 + this.form.level3 + this.form.mul) + Math.max(this.form.other - 1, 0);
        let tableData = this.tableData;
        for (let item of tableData) {
          item.totalScore = parseFloat(totalLevel * item['score']).toFixed(0);
          if (item.name == '天下') {
            this.airScore = item.totalScore;
          } else {
            this.otherScore = item.totalScore;
          }
        }
        let old = this.form.old * 10000;
        if (this.form.money > 0) {
          old += this.form.money * 3500;
        }
        this.old = old;
        this.calcTable();
      },
      // 计算缺少功绩
      calcTable() {
        const newTableData = [];
        for(const item of this.targets) {
          const value = Math.max(item.value - this.old, 0);
          const count = Math.floor(value / this.airScore);
          const otherCount = Math.floor(value / this.otherScore);
          newTableData.push({
            name: item.name, count: count, otherCount: otherCount
          });
        }
        this.newTableData = newTableData;
      }
    }
  };
</script>


