<template>
    <div id="tuesday">
      <el-card class="box-card">
        <el-form label-width="100px">
            <el-form-item label="府兵等级">
              <el-select @change="calcRate" v-model="soldier" filterable placeholder="请选择等级">
                <el-option label="无" :value="0"></el-option>
                <el-option v-for="(item, idx) in tables" :label="item.name" :value="item.value" :key="idx"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="争锋功绩对决">
              <el-select @change="calcRate" v-model="addtion" filterable placeholder="请选择等级">
                <el-option label="无" :value="0"></el-option>
                <el-option v-for="(item, idx) in addtions" :label="item.name" :value="item.value" :key="idx"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="是对决州">
              <el-switch
                v-model="isEnemy"
                @change="calcRate"
                active-color="#13ce66"
                inactive-color="#ff4949">
              </el-switch>
            </el-form-item>
          </el-form>
        <div slot="header" class="clearfix">
          <span>课业基本信息</span>
        </div>
        <el-table :data="tableData" style="width: 100%">
          <el-table-column prop="name" label="项目"></el-table-column>
          <el-table-column prop="score" label="基础分"></el-table-column>
          <el-table-column prop="totalScore" label="加倍分"></el-table-column>
        </el-table>
      </el-card>
      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <span>需要数量</span>
        </div>
        <el-table stripe :data="newTableData" style="width: 100%">
          <el-table-column align="center" prop="name" width="70" label="档位"></el-table-column>
          <el-table-column align="center" label="需要数量">
            <template slot-scope="scope">
              <el-card class="box-card">
                <div class="text item">
                  {{'嘎: ' + scope.row.diedCount }}万
                </div>
                <div class="text item">
                  {{'重: ' + scope.row.seriouslyCount}}万
                </div>
                <div class="text item">
                  {{'轻: ' + scope.row.minorsCount}}万
                </div>
              </el-card>
            </template>
          </el-table-column>
          <el-table-column align="center" width="60" label="达标">
            <template slot-scope="scope">
              <el-button v-if="scope.row.minorsCount == 0" type="success" size="small" icon="el-icon-check" circle></el-button>
              <el-button v-else size="small" icon="el-icon-close" circle></el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-card>
    </div>
</template>
<script>
  export default {
    props: {
      form: Object,
      targets: Array,
    },
    data() {
      return {
        activeName: '1',
        tableData: [],
        tables: [
          { name: '4级府兵', value: '4级府兵' },
          { name: '5级府兵', value: '5级府兵' },
          { name: '6级府兵', value: '6级府兵' },
          { name: '7级府兵', value: '7级府兵' },
          { name: '8级府兵', value: '8级府兵' },
          { name: '9级府兵', value: '9级府兵' },
          { name: '10级府兵', value: '10级府兵' },
          { name: '11级府兵', value: '11级府兵' },
          { name: '12级府兵', value: '12级府兵' }
        ].sort(() => -1),
        minors: [
          { name: '4级府兵', value: 1 },
          { name: '5级府兵', value: 2 },
          { name: '6级府兵', value: 3 },
          { name: '7级府兵', value: 4 },
          { name: '8级府兵', value: 5 },
          { name: '9级府兵', value: 6 },
          { name: '10级府兵', value: 7 },
          { name: '11级府兵', value: 8 },
          { name: '12级府兵', value: 9 },
        ],
        seriously: [
          { name: '4级府兵', value: 1 },
          { name: '5级府兵', value: 2 },
          { name: '6级府兵', value: 3 },
          { name: '7级府兵', value: 4 },
          { name: '8级府兵', value: 5 },
          { name: '9级府兵', value: 6 },
          { name: '10级府兵', value: 8 },
          { name: '11级府兵', value: 10 },
          { name: '12级府兵', value: 18 },
        ],
        died: [
          { name: '4级府兵', value: 4 },
          { name: '5级府兵', value: 5 },
          { name: '6级府兵', value: 6 },
          { name: '7级府兵', value: 7 },
          { name: '8级府兵', value: 8 },
          { name: '9级府兵', value: 10 },
          { name: '10级府兵', value: 12 },
          { name: '11级府兵', value: 15 },
          { name: '12级府兵', value: 36 },
        ],
        old: 0,
        newTableData: [],
        addtion: 0,
        addtions: Array.from({length: 10}, (item, index) => {return {name: `${index + 1}级`, value: (index + 1) * 0.15}}),
        soldier: 0,
        isEnemy: 0,
        diedScore: 0,
        seriouslyScore: 0, 
        minorsScore: 0
      };
    },
    watch: {
      form: {
        deep: true,
        handler(newVal, oldVal) {
          this.calcRate();
        }
      }
    },
    methods: {
      calcRate() {
        let totalLevel = (this.form.level1 + this.addtion + this.form.level2 + this.form.level3 + this.form.mul) + Math.max(this.form.other - 1, 0);
        if (this.isEnemy) {
          totalLevel += 3;
        }
        const tableData = [];
        const minorsItem = this.minors.filter((item, _) => item.name == this.soldier);
        const seriouslyItem = this.seriously.filter((item, _) => item.name == this.soldier);
        const diedItem = this.died.filter((item, _) => item.name == this.soldier);

        for (const item of minorsItem) {
          tableData.push({
            'name': '轻伤' + item.name, score: item.value, totalScore: totalLevel * item.value
          });
          this.minorsScore = totalLevel * item.value;
        }
        for (const item of seriouslyItem) {
          tableData.push({
            'name': '重伤' + item.name, score: item.value, totalScore: totalLevel * item.value
          });
          this.seriouslyScore = totalLevel * item.value;
        }
        for (const item of diedItem) {
          tableData.push({
            'name': '阵亡' + item.name, score: item.value, totalScore: totalLevel * item.value
          });
          this.diedScore = totalLevel * item.value;
        }
        this.tableData = tableData;
        let old = this.form.old;
        if (this.form.money > 0) {
          old += this.form.money * 3500;
        }
        this.old = old;
        this.calcTable();
      },
      // 计算缺少功绩
      calcTable() {
        let newTableData = [];
        for(const item of this.targets) {
          const value = Math.max(item.value - this.old, 0);

          const minorsCount = value / this.minorsScore;
          const seriouslyCount = value / this.seriouslyScore;
          const diedCount = value / this.diedScore;

          newTableData.push({
            name: item.name,
            minorsCount: (minorsCount / 10000).toFixed(1),
            seriouslyCount: (seriouslyCount / 10000).toFixed(1),
            diedCount: (diedCount / 10000).toFixed(1)
          });
        }
        this.newTableData = newTableData.sort(() => -1);
      }
    }
  };
</script>


